import * as Yup from 'yup'
import axios from '../../axios'
import {DropzoneArea} from 'material-ui-dropzone'
import Alert from '@material-ui/lab/Alert';
import  React, {Component} from 'react';
import { Formik, Form, Field } from 'formik';
import { Button,LinearProgress, Grid , Container, FormControl,InputLabel,MenuItem,Paper} from '@material-ui/core';
import { TextField, Select} from 'formik-material-ui';
import { connect } from 'react-redux'
const mapStateToProps = (state) => ({
  Dept_ID: state.Dept_ID,
  collegeID: state.collegeID
});

class AddFacultyDetails extends Component{
  constructor(props){
    super(props);
    this.state = {
      deptInfo: '',
      file:'',
      base64:'',
      fileerror: true,
    }
  };
handleFileChange=(files)=>{
    if(files[0]!==undefined)
    {       
      let reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onloadend = () => {
      this.setState({
          file: files[0],
          base64: reader.result,
          fileerror: false
        });
    }
    }
  }
  render(){
  return (
    <>
     <Container maxWidth='xl'>
     <Paper elevation={2} variant='elevation'>
         <div style={{'padding' : '2rem', 'marginLeft': '4rem', 'marginRight': '2rem'}}>
        <h4>Department Faculty Details</h4>
  <Formik
      initialValues={{
        Name: '',
        Designation: '',
        appointment_type: '',
        Qualification:'',
        Teaching_exp:'',
        Specialization:'',
        Mobile_no:'',
        email:'',
      }}
      
      validationSchema={Yup.object({
      Name: Yup.string()
          .required("Required"),
      Designation: Yup.string()
          .required("Required"), 
      appointment_type: Yup.string()
      .required("Required"), 
      Qualification: Yup.string()
      .required("Required"), 
      Teaching_exp: Yup.string()
      .required("Required"), 
      Specialization: Yup.string()
      .required("Required"), 
      Mobile_no: Yup.string()
      .required("Required"), 
     email: Yup.string()
      .required("Required"), 
      })
    }
       onSubmit=  {(values, { setSubmitting, resetForm }) => {
            setTimeout(() => {
            values.Photo=this.state.base64
            setSubmitting(false);       
            axios
              .post(
                "api/facultydetails/" +
                  this.props.Dept_ID +
                  "/" +
                  this.props.collegeID,
                JSON.stringify(values),
                {
                  headers: {
                    // Overwrite Axios's automatically set Content-Type
                    "Content-Type": "application/json",
                    Accept: "application/json",
                  },
                }
              )
              .then((res) => {
                if (res.status === 200) {
                  alert("Data Saved in Database :", res.data.flag);
                }
              })
              .catch(function (error) {
                alert("Error occured:" + error);
              });
              resetForm()
          }, 500);
        }   
    }   
    
    >
      {({ submitForm,
       isSubmitting,
        values }) => (
          
        <Form>
        <Grid container  direction={'row'} spacing={3}>
        <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="Name"          
            type="text"
            label="Name"
            margin='dense' 
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="Designation" 
            margin='dense'         
            type="text"
            label="Designation "
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}> 
         <FormControl variant="outlined" fullWidth  margin='dense'  required >
             <InputLabel htmlFor="gender-simple">Nature of Employment</InputLabel>
             <Field
               component={Select}
               name="appointment_type"
               label='Nature Of Employment'
               inputProps={{
                 id: 'gender-simple',
               }}
             >
               <MenuItem value={'Permanent'}>Permanent</MenuItem>
               <MenuItem value={'Academic Arrangement'}>Academic Arrangement</MenuItem>
               <MenuItem value={'Others'}>Others</MenuItem>
             </Field>
           </FormControl>
         </Grid>  
         <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="Qualification"          
            type="text"
            margin='dense' 
            label="Qualification"
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="Teaching_exp"          
            type="text"
            margin='dense' 
            label="Teaching Experience  (Years)"
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="Specialization"          
            type="text"
            margin='dense' 
            label="Area of Specialization"
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="Mobile_no"          
            type="text"
            margin='dense' 
            label="Mobile No"
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}>
          <Field
            component={TextField}
            name="email"          
            type="text"
            margin='dense' 
            label="Email ID"
            variant="outlined"
            fullWidth
            required
            />
        </Grid>
        <Grid item lg={4} xs={6}></Grid>
        <Grid item lg={4} xs={6}>
         {this.state.fileerror? <Alert severity="error"> Photograph of the Faculty (max 50Kbs) in JPEG Format only </Alert>:<Alert severity="info">Select PP Size Photograph of the Faculty (max 50Kbs) in JPEG </Alert>}
        <DropzoneArea
         acceptedFiles={['image/jpeg']}
         maxFileSize={50000}
         filesLimit={1}
         dropzoneText="Faculty Photograph"
         showPreviewsInDropzone = {true}
        onChange={this.handleFileChange}
        />
         </Grid>
         <Grid item lg={12} xs={12}> {isSubmitting && <LinearProgress />}</Grid>
         <Grid item lg={4} xs={0}></Grid>
         <Grid item lg={4} xs={6}>
        <Button
            variant="contained"
            color="primary"
            disabled={isSubmitting}
            onClick={submitForm}
        >
            Submit
        </Button>
        </Grid>
          <Grid item lg={4} xs={0}></Grid>
        </Grid> 
        </Form>
         
      )}
    </Formik>
    </div> 
    </Paper> 
    </Container>
  </>
  );
  
}}

export default connect(
  mapStateToProps,
)(AddFacultyDetails);
